'use strict';

// MODULES //

import isPlainObject from '@stdlib/assert-is-plain-object';
import hasOwnProp from '@stdlib/assert-has-own-property';
import isOrder from '@stdlib/ndarray-base-assert-is-order';
import contains from '@stdlib/array-base-assert-contains';
import orders from '@stdlib/ndarray-orders';
import join from '@stdlib/array-base-join';
import format from '@stdlib/error-tools-fmtprodmsg';


// MAIN //

/**
* Validates function options.
*
* @private
* @param {Object} opts - destination object
* @param {Array} dtypes - list of supported output data types
* @param {Object} options - options
* @param {*} [options.dtype] - output array data type
* @param {string} [options.order] - output array order
* @returns {(Error|null)} null or an error object
*
* @example
* var dtypes = [ 'float64', 'float32', 'generic' ];
*
* var opts = {};
* var options = {
*     'order': 'row-major'
* };
* var err = validate( opts, dtypes, options );
* if ( err ) {
*     throw err;
* }
*/
function validate( opts, dtypes, options ) {
	if ( !isPlainObject( options ) ) {
		return new TypeError( format( '0gz2V', options ) );
	}
	if ( hasOwnProp( options, 'dtype' ) ) {
		opts.dtype = options.dtype;
		if ( !contains( dtypes, String( opts.dtype ) ) ) {
			return new TypeError( format( '0gz4S', 'dtype', join( dtypes, '", "' ), opts.dtype ) );
		}
	}
	if ( hasOwnProp( options, 'order' ) ) {
		opts.order = options.order;
		if ( !isOrder( opts.order ) ) {
			return new TypeError( format( '0gz4S', 'order', join( orders(), '", "' ), opts.order ) );
		}
	}
	return null;
}


// EXPORTS //

export default validate;
